import { Component } from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  ActionSheetController,
  ModalController,
  LoadingController
} from 'ionic-angular';
import {Bmobconfig} from "../../common/getBmob";
import {Storage} from "@ionic/storage";
import {HttpClient} from "@angular/common/http";
import {BaseUI} from "../../common/baseui";

import{File} from "@ionic-native/file";
import {Transfer, TransferObject} from "@ionic-native/transfer";
import { FilePath } from '@ionic-native/file-path';
import { Camera , CameraOptions} from '@ionic-native/camera';

declare var cordova: any;

/**
 * Generated class for the HeadfacePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-headface',
  templateUrl: 'headface.html',
})
export class HeadfacePage extends BaseUI{
  userId:string;
  errorMessage:string;
  lastImg:string;
  path:string;
  img:any;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public modalCtrl: ModalController,
              public storage: Storage,
              public loadingCtrl: LoadingController,
              public http:HttpClient,
              public camera: Camera,
              public transfer: Transfer,
              public file: File,
              public filePath: FilePath,
              public actionSheetCtrl: ActionSheetController) {
    super();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HeadfacePage');
  }

  presentActionSheet()
  {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Choose a photo',
      buttons: [
        {
          text: 'Choose from albums',
          handler: () => {
            this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
            console.log('Choose from albums clicked');
          }
        },
        {
          text: 'Take a photo',
          handler: () => {
            this.takePicture(this.camera.PictureSourceType.CAMERA);
            console.log('Take a photo clicked');
          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });

    actionSheet.present();
  }

  takePicture(sourceType)
  {

    const options = {
      quality: 100,/** Picture quality in range 0-100. Default is 50 */
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      saveToPhotoAlbum: true,
      sourceType: sourceType, //take a picture or choose from album
    }

    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64 (DATA_URL):
      this.lastImg = imageData;
      // If base64:
      let base64Image = 'data:image/jpeg;base64,' + imageData;
      this.path = base64Image;
    }, (err) => {
      // Handle error
    });
  }

  uploadImg()
  {
    var loading = super.showLoading(this.loadingCtrl, "UPLoading...");
    this.storage.get('UserId').then(objectId => {
      var Bmob = Bmobconfig.getBmob();
      var upload = {
        'img': this.path
      }
      var file = Bmob.File("avatar.jpg", upload);
      file.save().then((obj) => {
        this.img = obj[0]['url'];
        var query = Bmob.Query('_User');
        return query.get(objectId)
      }).then(res=>{
        res.set('headimage', this.img);
        return res.save();

      }).then(()=>{
        loading.dismiss();
      })

    })
  }


}



